@import '../../styles/styles';

.registry {
	position: fixed;
	left: 0;
	top: 0;
	width: 100%;
	height: 100%;
	background: $bg_color;
	box-sizing: border-box;
	font-family: $main_font;
	color: $main_font_color;
	overflow-y: auto;
	overflow-x: hidden;

	@include respond-to('medium') {
		//color: rgb(204, 36, 36);
	}

	@include respond-to('small') {
		//color: rgb(31, 168, 13);
	}

	@include print {
		position: relative;
	}

	.content {
		margin-top: 64px;
	}
}

.header {
	background: $header_color;
	color: $light_font_color;
	display: flex;
	align-items: center;
	justify-content: space-between;
	height: 64px;
	padding: 0 47px 0 40px;
	position: fixed;
	right: 8px;
	left: 0;
	z-index: 120;

	@include respond-to('small') {
		padding: 0 24px 0 20px;
	}

	@include print {
		display: none;
	}
}

.logo {
	display: flex;
	align-items: center;
	&__image {
		line-height: 0;
		margin-right: 14px;
	}
	&__text {
		text-transform: uppercase;
		font-size: 12px;
	}
}

.user {
	display: flex;
	align-items: center;
	&__info {
		text-align: right;
		font-size: 13px;
	}
	&__name {
		margin-bottom: 1px;
	}
	&__position {
		opacity: 0.5;
	}
	&__image {
		margin-left: 14px;
		line-height: 0;
	}
}
